body {
  background-color: #def0ef;
  margin: 0;
  padding: 50px;
  font-family: Arial, sans-serif;
}

#form-container {
  justify-content: center;
  margin: 20px;
  display: flex;
}

h1 {
  text-align: center;
  color: #001f1f;
  margin-bottom: 15px;
  font-size: 1.5rem;
}

#board {
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  display: grid;
}

.column {
  background-color: #cde4e4;
  border: 1px solid #a3c8c5;
  border-radius: 8px;
  padding: 10px;
}

.column h2 {
  color: #003937;
  text-align: center;
  border-bottom: 1px solid #04a8ab;
  margin: 0 0 10px;
  padding-bottom: 5px;
}

.task {
  background-color: #e9fafc;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 8px 0;
  padding: 8px;
}

.task h3 {
  color: #333;
  margin: 0;
  font-size: 1rem;
}

.task p {
  color: #555;
  margin: 5px 0;
  font-size: .9rem;
}

.task small {
  color: #888;
  font-size: .8rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #004b4d;
  border: none;
  border-radius: 5px;
  margin-top: 5px;
  padding: 5px 8px;
  font-size: .9rem;
}

button:hover {
  background-color: #00a1b3;
}

button.delete {
  background-color: #dc3545;
}

button.delete:hover {
  background-color: #c82333;
}

#filters-container {
  background-color: #f0f8f8;
  border-radius: 8px;
  justify-content: center;
  gap: 20px;
  margin: 20px 0;
  padding: 15px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.filter-group {
  flex-direction: column;
  display: flex;
}

.filter-group label {
  color: #003937;
  margin-bottom: 5px;
  font-size: .9rem;
}

.filter-group select {
  background-color: #fff;
  border: 1px solid #9bd5d4;
  border-radius: 5px;
  min-width: 150px;
  padding: 6px;
}

form {
  background-color: #ceebed;
  border: 1px solid #9bd5d4;
  border-radius: 5px;
  margin-bottom: 30px;
  padding: 20px;
}

form input, form select {
  background-color: #ebffff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin: 5px 0;
}

form button {
  background-color: #027d81;
  width: 100%;
}

form button:hover {
  background-color: #015a55;
}

@media (width <= 768px) {
  .scrum-board {
    grid-template-columns: 1fr;
  }
}
